.section-contact-form {
	.section-heading {
		line-height: 1.2em;
	}
	form {
		>.lg-flex {			
			display: flex;
			flex-wrap: wrap;
		}
	}
	.form-field {
		flex: 0 0 100%;
		max-width: 100%;
		margin: 0 0 20px;
		display: flex;
		input {
			height: 46px;
		}
		input, textarea {
			background: var(--miscellaneous-color);
			font-size: 15px;
			border-radius: var(--border-radius);
			padding: 10px 15px;
		}
	}
	.form-button-group {
		min-width: 200px;
	}
	@media (min-width: 750px) {
		.section-heading {
			font-size: 30px;
		}
		form {
			>.lg-flex {
				.form-field {
					flex: 0 0 33.33%;
					max-width: 33.33%;
				}
			}
		}
	}
}